@import './reset.scss';

$mainColor: #ff0000;

.red {
    color: $mainColor;
}

// sass method 선언 @mixin
@mixin base-alert() {
    background: #eeeeee;
    padding: 20px;
    border-radius: 5px;
    max-width: 500px;
    width: 100%;
    margin: auto;
    p {
        margin-bottom: 0;
    }
}

// method call @include
.my-alert {
    @include base-alert()
}

// sass extend 문법 @extend
.my-alert-red {
    @extend .my-alert;
    background: #ffe593;
}

